html {
  --bluecolor: #00acdc;
  --blackcolor: #000;
}

body {
  text-transform: uppercase;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.header {
  justify-content: space-between;
  align-items: center;
  min-width: 1024px;
  margin: 0;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.nav__logo {
  height: 40px;
  margin-left: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin: 0 0 0 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:link, .nav__link:visited {
  color: var(--blackcolor);
}

.nav__link:hover, .nav__link:active {
  color: var(--bluecolor);
}

.is-active {
  color: var(--bluecolor);
  position: relative;
}

.is-active:link, .is-active:visited {
  color: var(--bluecolor);
}

.is-active:after {
  content: "";
  background-color: var(--bluecolor);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}
